import React from 'react'
import {Routes , Route} from 'react-router-dom';
import Home from './Home';
import Page2 from './page2/Page2';
function Routers() {
  return (
    <Routes>
           <Route path='/BLD.AI-Project2-Phase2/' element={<Home />}></Route>
           <Route path='/BLD.AI-Project2-Phase2/course/:id' element={<Page2></Page2>}></Route>
          
    </Routes>
  )
}

export default Routers